.App {
    text-align: center;
  }
  
  .operands, .operators{
    display: flex;
    overflow-x: scroll;
  }
  
  .operand, .operator, .comparator, .rhs{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    width: 100px;
    padding: 10px;
    margin: 10px;
    font-weight: bolder;
    background-color: yellowgreen;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    cursor: move;
    flex-grow: 0;
    flex-shrink: 0;
  }
  
  .operator, .comparator, .rhs{
    height: 50px;
    background-color: rosybrown;
  }
  
  .comparator, .rhs{
    cursor: pointer;
  }
  
  .canvas{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    border: dashed 5px grey;
    height: 150px;
    align-items: center;
  }
  
  .submit{
    color: white;
    font-weight: bold;
    border: none;
    background-color:cornflowerblue;
    padding: 10px;
    width: 100%;
    border-radius: 10px;
    margin-top: 20px;
    cursor:pointer;
  }
  
  .remove{
    display: flex;
    color: white;
    position: absolute;
    right: 10px;
    top: 10px;
    height: 15px;
    width: 15px;
    justify-content: center;
    align-items: center;
  }
  
  .remove:hover{
    color: bisque;
  }
  
  .canvas > .operand, .canvas > .operator, .canvas > .comparator{
    position: relative;
    cursor: pointer;
  }
  
  .canvas > .rhs{
    position: relative;
    cursor: pointer;
    height: 100px;
    background-color:darksalmon;
  }
  
  .space{
    width: 50px;
  }